<template>
  <!-- 与 ItemDetail 的 loading 状态一致，replace 到详情页时视觉连贯 -->
  <div class="classic-detail-page">
    <div class="page-bg">
      <div class="page-bg-gradient" />
    </div>
    <div class="detail-inner">
      <div class="loading-wrap">
        <el-icon class="is-loading" :size="32"><Loading /></el-icon>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { Loading } from '@element-plus/icons-vue'
import { browse } from '@/api/classic'

const router = useRouter()

onMounted(async () => {
  try {
    const list = await browse({ random: 1 })
    const first = list?.[0]
    if (first) {
      router.replace('/classic/item/' + first.id)
    } else {
      ElMessage.info('暂无数据，去句海漫游看看吧')
      router.replace('/classic/all')
    }
  } catch {
    ElMessage.error('获取失败')
    router.replace('/classic')
  }
})
</script>

